// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui' // 引入核心js组件
import 'element-ui/lib/theme-chalk/index.css' // 引入依赖的样式
import store from './store' // 引入前端拦截器

Vue.use(ElementUI) // 引用

// 要进入到项目文件夹安装axios模块：npm install --save axios
var axios = require('axios')

// 设置反向代理，前端请求默认发送到 http://localhost:8443/api
axios.defaults.baseURL = 'http://localhost:8443/api'

// 全局注册 之后可在其他组件中通过 this.$axios 发送数据
Vue.prototype.$axios = axios

//  axios 主动开启 withCredentials 功能,前端每次发送请求时就会带上 sessionId
//  shiro 就可以通过 sessionId 获取登录状态并执行是否登录的判断。
axios.defaults.withCredentials = true

Vue.config.productionTip = false

// 逻辑：首先判断访问的路径是否需要登录，如果需要，判断 store 里有没有存储 user 的信息
// 如果存在，则放行，否则跳转到登录页面，并存储访问的页面路径

// axios.get('/authentication')  页面拦截：
// 伪造参数，绕过前端的路由限制，访问本来需要登录才能访问的页面
router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {
    if (store.state.user.username) {
      axios.get('/authentication').then(resp => {
        if (resp.data()) next()
      })
    } else {
      next({
        path: 'login',
        query: {redirect: to.fullPath}
      })
    }
  } else {
    next()
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  render: h => h(App),
  router,
  store, // 添加拦截器
  components: { App },
  template: '<App/>'
})



